.dishMenuBackdrop {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 999;
  background-color: rgba(0, 0, 0, 0.3);
}

.addProductForm {
  width: 185px;
  background: #555;
  padding: 20px;
  -webkit-box-shadow: 0px 0px 23px 7px rgba(255, 255, 255, 0.8);
          box-shadow: 0px 0px 23px 7px rgba(255, 255, 255, 0.8);
}

.addProductInput {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  width: 180px;
  margin: 10px auto;
  height: 20px;
}

.nameFormBtn {
  vertical-align: top;
  width: 100px;
  height: 30px;
  color: #ccc;
  background: rgb(46, 46, 46);
  border: none;
  cursor: pointer;
}

.nameFormBtn:hover {
  color: #ccc;
  background: rgb(0, 0, 0);
}



